// src/components/ClientWrapper.js
'use client'

import { useEffect, useState } from 'react'
import dynamic from 'next/dynamic'

// Dynamically import MapComponent to avoid SSR issues
const MapComponent = dynamic(() => import('./MapComponents'), {
  ssr: false,
  loading: () => (
    <div className="flex items-center justify-center h-full bg-gray-100">
      <div className="text-center">
        <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-gray-800 mb-2 mx-auto"></div>
        <p className="text-sm text-gray-600">กำลังโหลดแผนที่...</p>
      </div>
    </div>
  )
})

export function ClientWrapper({ onLocationSelect }) {
  const [isClient, setIsClient] = useState(false)

  useEffect(() => {
    setIsClient(true)
  }, [])

  if (!isClient) {
    return (
      <div className="flex items-center justify-center h-full bg-gray-100">
        <div className="text-center">
          <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-gray-800 mb-2 mx-auto"></div>
          <p className="text-sm text-gray-600">กำลังเตรียมแผนที่...</p>
        </div>
      </div>
    )
  }

  return <MapComponent onLocationSelect={onLocationSelect} />
}